import { useState } from "react";
import { evaluate } from "mathjs";
import Button from "./Button";
import style from "./Home.module.css";

export default function Home() {
  const [res, setRes] = useState("");

  const buttons = [
    "C",
    ".",
    "/",
    "9",
    "8",
    "7",
    "*",
    "6",
    "5",
    "4",
    "+",
    "3",
    "2",
    "1",
    "-",
    "0",
    "DEL",
    "="
  ];
  const findVal = () => {
    const result = evaluate(res);
    // let result = Function("return " + res)();
    setRes(result.toString());
  };

  const handler = (num) => {
    console.log(num);
    if (num === "C") setRes("");
    else if (num === "=") findVal();
    else if (num === "DEL") {
      let disNum = res.length;
      if (disNum > 0) setRes(res.slice(0, disNum - 1));
    } else if (res === "Infinity") {
      setRes("");
      return;
    } else setRes(res.concat(num));
  };

  return (
    <div className={style.App}>
      <div className={style.inner}>
        <div className={style.result}>
          <div className={style.resbox}>{res}</div>
        </div>
        <div className={style.btns}>
          {buttons.map((btn, index) => {
            return <Button value={btn} key={index} handler={handler} />;
          })}
        </div>
      </div>
    </div>
  );
}
