import style from "./Button.module.css";
export default function Button({ value, handler }) {
  return (
    <div
      className={
        value === "C" || value === "="
          ? `${style.calcBtns} ${style.res}`
          : `${style.calcBtns}`
      }
      onClick={() => handler(value)}
    >
      {value}
    </div>
  );
}
